@import "../variables.modules";

main {

  @media only screen and (min-width: 1025px) {
    flex-direction: row;
    justify-content: center;
  }

  h1 {
    font-size: 2.5rem;
    text-align: center;
    @media only screen and (min-width: 940px) {
      font-size: 3rem;
      text-align: left;
    }
  }

  h2 {
    font-size: 1.5rem;
    text-align: center;
    @media only screen and (min-width: 940px) {
      font-size: 2rem;
      text-align: left;
    }
  }

  ul {
    padding: .8rem;
  }

  li {
    font-size: 1.2rem;
    line-height: 1.8;
  }

  .firstName {
    background: -webkit-linear-gradient(135deg, $purple, $pink);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .emoji {
    margin-right: .5rem;
    font-size: 1.5rem;
    @media only screen and (min-width: 940px) {
     margin-right: 1rem;
    }
  }
}

.avatar {
  width: 30vh;
  height: 30vh;
  border-radius: 50%;
  background: linear-gradient(to bottom right, $pink, $purple);
  padding: 0.5rem;
  opacity: 0.9;
  transition: opacity 700ms;
  margin-bottom: 1rem;

  @media only screen and (min-width: 940px) {
    width: 50vh;
    height: 50vh;
    margin-right: 3rem;
    margin-bottom: none;
  }

  &:hover {
    opacity: 1;
  }
}

.socials {
  display: flex;
  justify-content: space-evenly;
  font-size: 2.5rem;
  padding: 1rem 0;

  a {
    color: $dark;
    transition: color 700ms;
  }

  a:hover {
    color: $purple;
  }
}

a, a:visited {
  color: $dark;
}